___CSS_LOADER_EXPORT___.push([module.id, `@media screen and (min-width: 650px) {
  app-bar {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  app-bar .app-bar__menu {
    display: none;
  }

  app-bar .app-bar__navigation {
    position: relative;
    top: 0;
    left: 0;
    width: auto;
    display: flex;
    line-height: 28px;
    background-color: transparent;
  }

  app-bar .app-bar__navigation ul {
    display: flex;
  }

  app-bar .app-bar__navigation ul li {
    margin: auto 44px;
    font-weight: 600;
    font-size: 18px;
  }

  app-bar .app-bar__navigation ul li a {
    display: inline-block;
    color: #ca4e79;
    padding: 8px;
    margin-bottom: 5px;
    width: 100%;
  }

  app-bar .app-bar__navigation ul li a:hover {
    color: #f7dfff;
    background-color: #ca4e79;
    border-radius: 50px;
  }
}

@media screen and (min-width: 700px) {
  body {
    font-size: 18px;
  }

  .inner .title {
    font-size: 40px;
  }

  .restaurants__list {
    grid-template-columns: repeat(2, 1fr);
    gap: 1rem;
  }

  .restaurant__info,
  .restaurant__menus .menu-category {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
  }

  .restaurant__poster img {
    width: 50vh;
    object-fit: cover;
  }
}

@media screen and (min-width: 900px) {
  .restaurants__list {
    grid-template-columns: repeat(3, 1fr);
  }
}

@media screen and (min-width: 1200px) {
  .inner .title {
    font-size: 50px;
  }

  .jumbotron img {
    width: 1000px;
    min-width: 100%;
  }

  .restaurant__poster img {
    width: 100%;
    object-fit: cover;
  }

  #mainContent {
    width: 1200px;
    margin: 0 auto;
  }
}`, "",{"version":3,"sources":["webpack://./src/styles/responsive.css"],"names":[],"mappings":"AAAA;EACE;IACE,aAAa;IACb,8BAA8B;IAC9B,mBAAmB;EACrB;;EAEA;IACE,aAAa;EACf;;EAEA;IACE,kBAAkB;IAClB,MAAM;IACN,OAAO;IACP,WAAW;IACX,aAAa;IACb,iBAAiB;IACjB,6BAA6B;EAC/B;;EAEA;IACE,aAAa;EACf;;EAEA;IACE,iBAAiB;IACjB,gBAAgB;IAChB,eAAe;EACjB;;EAEA;IACE,qBAAqB;IACrB,cAAc;IACd,YAAY;IACZ,kBAAkB;IAClB,WAAW;EACb;;EAEA;IACE,cAAc;IACd,yBAAyB;IACzB,mBAAmB;EACrB;AACF;;AAEA;EACE;IACE,eAAe;EACjB;;EAEA;IACE,eAAe;EACjB;;EAEA;IACE,qCAAqC;IACrC,SAAS;EACX;;EAEA;;IAEE,aAAa;IACb,qCAAqC;IACrC,SAAS;EACX;;EAEA;IACE,WAAW;IACX,iBAAiB;EACnB;AACF;;AAEA;EACE;IACE,qCAAqC;EACvC;AACF;;AAEA;EACE;IACE,eAAe;EACjB;;EAEA;IACE,aAAa;IACb,eAAe;EACjB;;EAEA;IACE,WAAW;IACX,iBAAiB;EACnB;;EAEA;IACE,aAAa;IACb,cAAc;EAChB;AACF","sourcesContent":["@media screen and (min-width: 650px) {\r\n  app-bar {\r\n    display: flex;\r\n    justify-content: space-between;\r\n    align-items: center;\r\n  }\r\n\r\n  app-bar .app-bar__menu {\r\n    display: none;\r\n  }\r\n\r\n  app-bar .app-bar__navigation {\r\n    position: relative;\r\n    top: 0;\r\n    left: 0;\r\n    width: auto;\r\n    display: flex;\r\n    line-height: 28px;\r\n    background-color: transparent;\r\n  }\r\n\r\n  app-bar .app-bar__navigation ul {\r\n    display: flex;\r\n  }\r\n\r\n  app-bar .app-bar__navigation ul li {\r\n    margin: auto 44px;\r\n    font-weight: 600;\r\n    font-size: 18px;\r\n  }\r\n\r\n  app-bar .app-bar__navigation ul li a {\r\n    display: inline-block;\r\n    color: #ca4e79;\r\n    padding: 8px;\r\n    margin-bottom: 5px;\r\n    width: 100%;\r\n  }\r\n\r\n  app-bar .app-bar__navigation ul li a:hover {\r\n    color: #f7dfff;\r\n    background-color: #ca4e79;\r\n    border-radius: 50px;\r\n  }\r\n}\r\n\r\n@media screen and (min-width: 700px) {\r\n  body {\r\n    font-size: 18px;\r\n  }\r\n\r\n  .inner .title {\r\n    font-size: 40px;\r\n  }\r\n\r\n  .restaurants__list {\r\n    grid-template-columns: repeat(2, 1fr);\r\n    gap: 1rem;\r\n  }\r\n\r\n